<template>
<div class="advinfo">
    <div class="advinfo-edit">
        <div class="advinfo-hashtag">
            <div class="inputname">
                <span>活動Hashtag</span>
            </div>
            <div class="hashtag-inputwrap">
                <div class="wrap">
                    <div class="tag" v-for="(item,index) of taglist" :key="index">
                        <span>{{item}}</span>
                        <div class="closebtnwrap" @click="deletage(index)">
                            <i class="ri-close-line"></i>
                        </div>

                    </div>
                    <input class="taginput" id="item" @keyup.enter="addtag" type="text" v-model="tagcontent">
                </div>

            </div>
        </div>
        <div class="advinfo-describeBlock">
            <h4>活動描述</h4>
            <textarea id="simplemde"></textarea>
            <!-- <vue-simplemde v-model="content" :configs="configs" /> -->
            <!-- <DescribeBlock @contenthandler="catchinputcontent"></DescribeBlock> -->
        </div>
        <div class="advinfo-fee">
            <div class="fee-btnwrap">
                <div class="fee-title">
                    <span>是否收費</span>

                </div>
                <div class="fee-yes">
                    <span>是</span>
                    <label class="container">
                        <input type="radio" @change="checkactfee" value='true' v-model="isfree">
                        <span class="checkmark"></span>

                    </label>
                </div>
                <div class="fee-no">
                    <span>否</span>
                    <label class="container">
                        <input @change="checkactfee" type="radio" value='false' v-model="isfree">
                        <span class="checkmark"></span>

                    </label>
                </div>

            </div>
            <div class="fee-yesblock" v-show="isfree=='true'">

                <InputOutline width="327" @inputtexthandler="catchprice" placeholder="輸入每人費用"></InputOutline>
                <div class="yesblock-title">
                    <span>選擇付款方式</span>
                </div>
                <div class="center" style="margin-top:17px">
                    <div class="center">
                        <CheckBox title="協會現場繳費" @emitcheck="catchvalue"></CheckBox>
                        <!-- <input value="現場" type="checkbox" v-model="feemode"> -->
                    </div>
                    <div class="center">
                        <CheckBox title="匯款" @emitcheck="catchvalue"></CheckBox>
                        <!-- <input value="匯款" type="checkbox" v-model="feemode"> -->
                    </div>
                </div>
                <InputOutline width="327" @inputtexthandler="catchaccountinfo" placeholder="輸入匯款資訊" v-show="feemode.includes('匯款')"></InputOutline>

            </div>

        </div>
    </div>
    <div class="advinfo-btnwrap">
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="取消" @clickhandler="upstep" class="cancelbtn"></fillbtn>
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="下一步" @clickhandler="nextstep"></fillbtn>
    </div>
</div>
</template>

<script>
// import DescribeBlock from '../components/Describeblock'
// import VueSimplemde from 'vue-simplemde'
// import "font-awesome/css/font-awesome.min.css";
// import "simplemde/dist/simplemde.min.css";
import SimpleMDE from "simplemde";
// import showdown from "showdown";
import fillbtn from '../components/Fillbtn'
import InputOutline from '../components/Inputoutline'
import CheckBox from '../components/Checkbox'
export default {
    data() {
        return {
            tagcontent: '',
            taglist: [],
            posttaglist: '',
            isfree: '',
            feemode: [],
            configs: {
                autofocus: false,
                status: false,
                spellChecker: false,
                renderingConfig: {
                    codeSyntaxHighlighting: true,
                    highlightingTheme: 'atom-one-light'
                },
                lineWrapping: true,
                placeholder: '在此輸入活動描述',
                toolbar: ['heading-1', 'heading-2', 'heading-3', "bold", "italic", "ordered-list", "clean-block", "link", "image", "|", "preview", "side-by-side", "fullscreen"]

            },
            content: '',
            htmlcontent: ''
        }
    },
    mounted() {
        var simplemde = new SimpleMDE({
            el: document.getElementById('simplemde'),
            autofocus: false,
            status: false,
            spellChecker: false,
            renderingConfig: {
                codeSyntaxHighlighting: true,
                highlightingTheme: 'atom-one-light'
            },
            lineWrapping: true,
            placeholder: '在此輸入活動描述',
            toolbar: ['heading-1', 'heading-2', 'heading-3', "bold", "italic", "ordered-list", "clean-block", "link", "image", "|", "preview", "side-by-side", "fullscreen"]
        })
        simplemde.codemirror.on("change", () => {
            this.content = simplemde.value()
            this.htmlcontent = SimpleMDE.prototype.markdown(this.content)
        })
    },
    methods: {

        addtag() {
            if (this.taglist.length < 3) {
                this.taglist.push(this.tagcontent)
                this.tagcontent = ''
            } else {
                alert('一次只能加三個標籤歐！')
            }

        },
        checkactfee() {
            let postdata = {
                datatype: 'fee',
                data: ''
            }
            if (this.isfree == 'true') {
                console.log(this.isfree)
                postdata.data = '是'
            } else if (this.isfree == 'false') {
                console.log(this.isfree)
                postdata.data = '否'
            }
            this.$store.commit('setactinfo', postdata)
        },
        catchvalue(val) {
            if (this.feemode.includes(val)) {
                let index = this.feemode.indexOf(val)
                this.feemode.splice(index, 1)
            } else {
                this.feemode.push(val);
            }
            let chargemethodscontent = ''
            for (let i = 0; i < this.feemode.length; i++) {
                chargemethodscontent = chargemethodscontent + this.feemode[i] + '、'
            }
            chargemethodscontent = chargemethodscontent.substring(0, chargemethodscontent.length - 1)
            let postdata = {
                datatype: 'chargemethods',
                data: chargemethodscontent
            }
            this.$store.commit('setactinfo', postdata)

        },
        catchaccountinfo(val) {
            let postdata = {
                datatype: 'accountinfo',
                data: val
            }
            this.$store.commit('setactinfo', postdata)
        },
        catchprice(val) {
            let postdata = {
                datatype: 'price',
                data: val
            }
            this.$store.commit('setactinfo', postdata)
        },
        upstep() {
            this.$store.commit('minuscreateactindex')
        },
        nextstep() {
            this.$store.commit('addcreateactindex')

            //處理taglist轉為字串，呼叫mutation去新增資料
            let posttagcontent = ''
            for (let i = 0; i < this.taglist.length; i++) {
                posttagcontent = posttagcontent + this.taglist[i] + '、'
            }
            posttagcontent = posttagcontent.substring(0, posttagcontent.length - 1)
            let postdata = {
                datatype: 'tag',
                data: posttagcontent
            }
            let postdata2 = {
                datatype: 'describe',
                data: this.htmlcontent
            }
            this.$store.commit('setactinfo', postdata)
            this.$store.commit('setactinfo', postdata2)
        },
        deletage(index) {
            this.taglist.splice(index, 1)
        }
    },
    components: {
        fillbtn,
        InputOutline,
        // VueSimplemde,
        CheckBox
    }
}
</script>

