<template>
<div class="basicinfo">
    <div class="basicinfo-editaera">
        <div class="basicinfo-actname">
            <inputoutline placeholder="照片連結" @inputtexthandler="catchinputcontent" headername="活動封面照片"></inputoutline>
            <!-- <div class="uploadpic-area">
                <div class="uploadbtn">
                    <span>上傳照片</span>
                </div>
                <div class="defaultpic">
                    <span>系統預設圖片</span>
                </div>
            </div> -->

        </div>
        <div class="basicinfo-actname">
            <inputoutline placeholder="輸入活動名稱" @inputtexthandler="catchinputcontent" headername="活動名稱"></inputoutline>
        </div>
        <div class="basicinfo-actname">
            <inputoutline placeholder="YYYY/MM/DD" @inputtexthandler="catchinputcontent" headername="活動日期"></inputoutline>
        </div>
        <div class="basicinfo-actname">
            <inputoutline placeholder="上午 hh:mm - 下午 hh:mm" @inputtexthandler="catchinputcontent" headername="活動時間"></inputoutline>
        </div>
        <div class="basicinfo-actname">
            <inputoutline placeholder="台北市中正區..." @inputtexthandler="catchinputcontent" headername="活動地點"></inputoutline>
        </div>
        <div class="basicinfo-actname">
            <inputoutline @inputtexthandler="catchinputcontent" headername="活動上限人數"></inputoutline>
        </div>
        <div class="basicinfo-actname">
            <inputoutline @inputtexthandler="catchinputcontent" headername="備取人數"></inputoutline>
        </div>
    </div>

    <div class="basicinfo-btnwrap">
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="取消" @clickhandler="upstep" class="cancelbtn"></fillbtn>
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="下一步" @clickhandler="nextstep"></fillbtn>
    </div>
</div>
</template>

<script>
import inputoutline from '../components/Inputoutline'
import fillbtn from '../components/Fillbtn'
export default {
    data() {
        return {

        }
    },
    methods: {
        upstep() {
            this.$store.commit('minuscreateactindex')
        },
        nextstep() {
            this.$store.commit('addcreateactindex')
        },
        catchinputcontent(inputcontent, inputheader) {
            let postdata = {
                datatype: 'type',
                data: inputcontent
            }

            switch (inputheader) {
                case '活動封面照片':
                    postdata.datatype = 'actpic';
                    this.$store.commit('setactinfo', postdata);
                    break;
                case '活動名稱':
                    postdata.datatype = 'actname';
                    this.$store.commit('setactinfo', postdata);
                    break;
                case '活動日期':
                    postdata.datatype = 'date';
                    this.$store.commit('setactinfo', postdata)
                    break;
                case '活動時間':
                    postdata.datatype = 'time';
                    this.$store.commit('setactinfo', postdata)
                    break;
                case '活動地點':
                    postdata.datatype = 'loc';
                    this.$store.commit('setactinfo', postdata)
                    break;
                case '活動上限人數':
                    postdata.datatype = 'quota';
                    this.$store.commit('setactinfo', postdata)
                    break;
                case '備取人數':
                    postdata.datatype = 'available';
                    this.$store.commit('setactinfo', postdata)
                    break;
            }
            console.log(inputcontent, inputheader);

        }
    },
    components: {
        inputoutline,
        fillbtn
    }
}
</script>

