<template>
<nav>
    <div class="logo">
        <img src="../assets/logo.svg" alt="">
    </div>
    <div  :class="{clicked:index==currentindex}" v-for="(item,index) of navlist" :key="index" >
        <div class="nav-item" @click="navto(item.name)">
            <i :class="item.icon"></i>
            <span>{{item.name}}</span>
        </div>

    </div>
</nav>
</template>

<script>
export default {
    data() {
        return {
            navlist: [{
                    icon: 'ri-group-2-line',
                    name: '會員管理'
                },
                {
                    icon: 'ri-calendar-event-line',
                    name: '活動管理'
                },
                {
                    icon: 'ri-settings-3-line',
                    name: '設定'
                }
            ],
            currentindex: 0
        }
    },
    methods: {
        navto(val) {

            console.log(this.currentindex, val)
            if (val == '設定') {
                this.currentindex = 2;
                this.$router.push({
                    name: 'Setting',
                }).catch(err => {
                    console.log(err)
                })
            } else if (val == '會員管理') {
                this.currentindex = 0;
                this.$router.push({
                    name: 'ActivityPage',
                    params: {
                        hostid: this.hostid
                    }
                })
            }
        }
    },
    computed: {
        hostid() {
            return this.$store.state.userinfo[3]
        }
    }
}
</script>

